table {
    position: absolute;
    top: 100px;
    right: 20px
}

@media screen and (max-width: 1450px) {

    table{
        position: sticky;
    }


}

@media screen and (max-width: 670px) {

    table{
       margin-left: 25%;
    }
    

}
@media screen and (max-width:458px) {

    table{
       margin-left: 15%;
    }
    

}
@media screen and (max-width:380px) {

    table{
       margin-left: 5%;
    }
    

}